html, body, header, nav, h1, a,
ul, li, strong, main, button, i,
section, img, div, h2, p, form,
fieldset, label, input, textarea,
span, article, footer, time, small {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font: inherit;
  color: inherit;
  text-align: inherit;
  text-decoration: inherit;
  vertical-align: inherit;
  box-sizing: inherit;
  background: transparent;
}

ul {
  list-style: none;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

input[type="password"],
input[type="email"],
input[type="text"],
input[type="submit"],
textarea,
button {

  -webkit-appearance: none;
  -moz-appearance: none;
}

button,
input[type="submit"] {
  cursor: pointer;
}

html {
  background: black;
  color:white;
}

.tile {
  height: 40px;
  width: 40px;
}

.blue {
  background: blue;
}

.green {
  background: green;
}

.red {
  background: red;
}

.orange {
  background: orange;
}

.yellow {
  background: yellow;
}

.tile:hover {
  cursor: pointer;
}

.row {
  display: flex;
  width: 500px;
  margin: 0 auto;
}

.score {
  font-size: 20px;
  font-family: 'Press Start 2P', cursive;
  line-height: 2;
  text-align: left;
}

.score-number {
  text-align: center;
  font-size: 30px;
}


.new-game {
  font-family: 'Press Start 2P', cursive;
  border: 1px solid white;
  border-radius: 5px;
  font-size: 10px;
  padding: 10px;
  transition: .5s;
  margin-top: 15px;
  width: 33%;
}

.new-game:hover {
  background: white;
  color: black;
}

.instructions {
  text-align: left;
  padding-left: 32px;
  line-height: 1.5;
  font-family: "Press Start 2P", cursive;
  font-size: 12px;
}

.title {
  text-align: center;
  font-family: 'Press Start 2P', cursive;
  padding: 15px;
  font-size: 35px;
}

.main {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  text-align: center;
}

.button-toggle {
  font-family: 'Press Start 2P', cursive;
  border: 1px solid white;
  border-radius: 5px;
  font-size: 10px;
  padding: 10px;
  margin-top: 35%;
  margin-bottom: 15px;
  width: 80%;
  transition: .5s;
}

.button-toggle:hover {
  background: white;
  color: black;
}

