.title {
  text-align: center;
  font-family: 'Press Start 2P', cursive;
  padding: 15px;
  font-size: 35px;
}

.main {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  text-align: center;
}

.button-toggle {
  font-family: 'Press Start 2P', cursive;
  border: 1px solid white;
  border-radius: 5px;
  font-size: 10px;
  padding: 10px;
  margin-top: 35%;
  margin-bottom: 15px;
  width: 80%;
  transition: .5s;
}

.button-toggle:hover {
  background: white;
  color: black;
}
